.profile .large-avatar {
  margin: 20px 60px;
}
.profile .large-avatar > img {
  width: 100%;
  border-radius: 50%;
}
.profile .row {
  border-top: solid 1px #eee;
  border-bottom: solid 1px #eee;
  color: #555;
}
.profile .col {
  text-align: center;
}
.profile .col .icon {
  display: inline-block;
  margin-right: 10px;
  transform: scale(1.5);
}
.profile .item-divider {
  display: block;
  margin-top: 40px;
  text-align: center;
  text-transform: uppercase;
  font-size: 10px;
  font-weight: bold;
  color: #777;
  background-color: transparent;
}
.profile .item-divider:first-child {
  margin-top: 10px;
}
.profile .item-divider .icon {
  display: block;
  font-size: 20px;
}
.profile .title {
  margin-bottom: 0;
}
.profile .author {
  display: block;
  font-size: 10px;
  font-weight: bold;
  text-transform: uppercase;
  color: #777;
}
